.navbar_container {
    height: 11vh;
    display:flex;
    align-items: center;
    position:fixed;
    width: 100%;
    background-color: #0e1111;
    z-index: 5;
}
.header-container {
    width: 90%;
    display: flex;
    margin:0 auto;
    flex-direction: row;
    justify-content: space-between;    
}
.logo_container {
    display:flex;
    flex-direction: row;
    align-content: center;
    justify-content: center;
}

.logo_container h1 {
    font-size: 1.4em;
}

.logo-title {
    display: flex;
    align-self: center;
    color: #DAE1E7
}

.yellow {
    color: #BD4B4B;
    font-weight:bolder
}

.logo_container .logo {
    margin-right: 0.3rem;
    margin-left: 0.5rem;
    color:#BD4B4B;
    
}

.menu-burger {
    color:#BD4B4B;
}
.navbar-container {
    display:flex;
    width: 70%;
    position:fixed;
    background-color: var(--black);
    left:0;
    height: 100vh;
    bottom:0.5px;
    z-index: 5;
}
.nav-menu {
    width: 100%;
    display: flex;
    list-style: none;
    align-self: center;
    color:var(--white);
    flex-direction: column;
    font-size:1em;
}

.nav-menu li  {
    display: flex;
    justify-content: center;
    margin: 1.2rem;
}
.nav-menu li a {
    font-size: 1em;
    text-decoration: none;
}


.nav-menu li a:hover {
    color:#BD4B4B;
    transition: 0.1s;
}

.menu-burger {
    cursor:pointer;
    
}

.cart-div {
    display: flex;
    border-radius: 50%;
    align-items: center;
    padding:1em;
    
}


.menu-cart {
    width:30%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    cursor:pointer;
    align-items: center;
}

.menu-cart p {
color: var(--white)

}


.cart-icon {
    color:var(--white);
}



.d-none {
    transform: translateX(-100%);
    transition: 0.5s;   
}


.d-flex {
  transform: translateX(0);
  transition: 1s;
}

@media (min-width: 992px){

    .navbar-container {
        width: 40%;
        
    }
    .logo_container h1 {
        font-size: 2.3em;
    }

    .menu-cart {
        width: 15%;
        display: flex;
        justify-content: space-between;
    }

 
    .cart-icon {
        display: inline-flex;
        padding: 0;
        width: auto;

    }
    .cart-div p {
        font-size: 1rem;
        font-weight: bold;
    }
    .cart-div:hover{
        background-color:#BD4B4B;
        transition: 0.4s;
    }
    .nav-menu {
        font-size: 1.8em;
    }

}

@media screen and (min-width: 1250px){
    .navbar-container {
        width: 35%;
        
    }
}